.card-container-profile {
  background-color: #dcdee7;
  border-radius: 5px;
  box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
  color: #2e2c2c;
  padding-top: 30px;
  position: relative;
  width: 350px;
  max-width: 100%;
  text-align: center;
}

.centered {
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

#profile-btn {
  text-decoration: none;
  color: #2e2c2c;
}

.card-container-profile .pro {
  color: #231e39;
  background-color: #fce2d3;
  border-radius: 3px;
  font-size: 14px;
  font-weight: bold;
  padding: 3px 7px;
  position: absolute;
  top: 30px;
  left: 30px;
}

.card-container-profile .round {
  border: 1px solid #fce2d3;
  border-radius: 50%;
  padding: 7px;
}

button.primary-profile {
  background-color: #d69b7b;
  border: 1px solid #d69b7b;
  border-radius: 3px;
  color: #2e2c2c;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  padding: 10px 25px;
  text-decoration: none;
}

.page404Styling {
  margin-top: 10px;
  background-color: #d69b7b;
  border: 1px solid #d69b7b;
  border-radius: 3px;
  color: #2e2c2c;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  padding: 10px 25px;
  text-decoration: none;
}

.centered404 {
  display: flex;
  align-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
}

button.primary-profile.ghost {
  background-color: transparent;
  color: #2e2c2c;
}

.skills-profile {
  background-color: #e7e7e7a5;
  text-align: left;
  padding: 15px;
  margin-top: 30px;
  border-radius: 5px;
}

.skills-profile ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.skills-profile ul li {
  border: 1px solid #2d2747;
  border-radius: 2px;
  display: inline-block;
  font-size: 12px;
  margin: 0 7px 7px 0;
  padding: 7px;
}

.pro-button {
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  color: black;
}

.dimProfile {
  height: 150px;
